import React, { useState, useEffect, useContext, Fragment } from "react";
import Footer from "../footer/Footer";
import Pagination from "@mui/material/Pagination";
import Stack from "@mui/material/Stack";
import PaginationContext from "../../../contex/productPagination/PaginationContext";
import { allProductsPublic } from "../../../service/api/productService";
import CardProduct from "./CardProduct";
import { useLocation } from "react-router-dom";
import Grid from "@mui/material/Grid";
import {scrollId} from "../../../service/utils/funtions";
import Box from "@mui/material/Box";
import LinearProgress from "@mui/material/LinearProgress";

const ProductsViews = () => {
  const [products, setProducts] = useState([]);
  const location = useLocation();
  const [statusProgress, setStatusProgress] = useState(true);
  const { numPage, updateNumPage, total, updateTotal } =
    useContext(PaginationContext);

  const onChangePagination = (event, page) => {
    updateNumPage(page - 1);
    scrollId("prodtucts-help-scroll-top");
  };

  useEffect(() => {
    const initial = async () => {
      try {
        var res;
        var actualLoacation = 0;
        switch (location.pathname) {
          case "/all/maquinarias":
            actualLoacation = 1;
            break;
          case "/all/invernaderos":
            actualLoacation = 2;
            break;
          case "/all/riegos":
            actualLoacation = 3;
            break;
          case "/all/laboratorios":
            actualLoacation = 4;
            break;
        }
        setStatusProgress(true);
        if (location.state !== undefined) {
          res = await allProductsPublic(location.state, numPage, 12);
        } else {
          res = await allProductsPublic(actualLoacation, numPage, 12);
        }
        setProducts(res.content);
        updateTotal(res.totalPages);
        setStatusProgress(false);
      } catch (error) {
        console.log(error);
      }
    };
    initial();
  }, [numPage, location.state]);
  return (
    <Fragment>
      <div id="prodtucts-help-scroll-top" style={{ background: "#cfe8fc" }}></div>
      <Grid
        sx={{ bgcolor: "#eeeeee", width: "100%" }}
        container
        alignItems="center"
        rowSpacing={3}
        columns={12}
        columnSpacing={{ xs: 1, sm: 1, md: 1, mt: 2 }}
      >
        <Grid item md={12} sx={{ height: "30px" }} align="center"></Grid>

        {statusProgress&&<Box sx={{ width: "100%" }}>
          <LinearProgress />
        </Box>}
        {products.map((product, index) => (
          <Grid key={index} align="center" item xs={12} sm={4} md={3}>
            <CardProduct key={index} product={product} />
          </Grid>
        ))}
        <Grid item md={12} align="center">
          <Stack spacing={2}>
            <Pagination
              sx={{}}
              onChange={onChangePagination}
              count={total}
              color="primary"
            />
          </Stack>
        </Grid>
      </Grid>

      <Footer />
    </Fragment>
  );
};

export default ProductsViews;
