import React,{useState} from 'react';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import {makeStyles} from '@mui/styles';
import Invernadero from '../../../assets/700X500/Invernadero.jpg';
import Laboratorio from '../../../assets/700X500/Laboratorio.jpg';
import Maquinaria from '../../../assets/700X500/Maquinaria.jpg';
import Riego from '../../../assets/700X500/Riego.jpg';


const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const useStayles = makeStyles({
  root: {
    
    

  },
  slide: {
    width: '450px',
    height: '300px',
    overflow: 'hidden',
    borderRadius:"10px"
  },
  imgStyle:{
    width: '100%',
    height: 'auto',
  }
});

const DemoAutoPlay=()=> {
  const [images, setImages] = useState([Invernadero,Laboratorio,Maquinaria,Riego])
  const [index, setIndex] = useState(0);
  const classes = useStayles();

  const handleChangeIndex = (index) => {
    setIndex(index);
    console.log(index)
  };

    return (
        <AutoPlaySwipeableViews enableMouseEvents index={index} onChangeIndex={handleChangeIndex}>
          {images.map((image, index)=>(
            <div key={index} className={ classes.slide}><img className={classes.imgStyle} src={image}></img></div>
          ))}
        </AutoPlaySwipeableViews>
    );
}

export default DemoAutoPlay;
