/**IMAGES */

export async function sendImages(id, formData, token) {
  const config = {
    headers: { "x-access-token": token, "Content-Type": "multipart/form-data" },
  };

  try {
    const res = await API.post(
      "/products/images/upload/" + id,
      formData,
      config
    );
    return res.data;
  } catch (error) {
    console.log(error);
  }
}

