import React, { useContext } from 'react'
import { HashRouter, Route, Switch } from 'react-router-dom';
import UserRoutes from './router/User.routes';
import AppBarUser from './components/appbar/AppBar';
import './scss/style.scss';

import UserContext from './contex/user/UserContext';




const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const AdminDefaultLayout = React.lazy(() => import('./layout/AdminDefaultLayout'))


const App =()=>  {
  const {adminArea,isAdmin} = useContext(UserContext);
  
    return (
      <HashRouter>
        
        <React.Suspense fallback={loading}>
          {!adminArea &&<AppBarUser/>}
          <Switch>
            {isAdmin&&<Route path="/admin" name="Admin" render={(props) => <AdminDefaultLayout {...props} />} />}
            <UserRoutes/>
          </Switch>
        </React.Suspense>
       
      </HashRouter>
    )
  
}

export default App
