import React, { useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import { IconLocation } from './IconLocation'
import 'leaflet/dist/leaflet.css'


function MapView() {
    const [state, setState] = useState({
        currentLocation: { lat: '-17.40130', lng: '-66.15194' },
        zoom: 18
    })
    return (

        <MapContainer center={state.currentLocation} zoom={state.zoom} style={{ p: 2, margin: 'auto', maxWidth: 1100, flexGrow: 1,  height: 335, justifyContent:'center', display:'flex', alignItems:'center' }}>
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <Marker position={[-17.40130, -66.15194]} icon={IconLocation}>
                    <Popup>
                        <strong>SMARTPLANTS</strong> <br /> C. Honduras N°630 entre <br />
                        C. Lanza y Av. República.
                    </Popup>
                </Marker>
            
        </MapContainer>

    );

}

export default MapView;