import React, { Fragment, useContext } from "react";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import LoginIcon from "@mui/icons-material/Login";
import { scrollId } from "../../service/utils/funtions.js";
import Options from "./_navUser";
import Logo from "./Logo";
import { makeStyles } from "@mui/styles";
import { useHistory } from 'react-router-dom';
import UserContext from "../../contex/user/UserContext";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';

const useStyles = makeStyles({
  styleDivider: {
    color: "#44AA00",
  },
});

const DrawerContent = ({ toggleDrawerLogin, toggleDrawer }) => {
  const classes = useStyles();
  const history = useHistory();
  const { online, isAdmin } = useContext(UserContext);
  const redirect = (item) => {
    console.log(item);
    history.push({
      pathname: item.to,
      state: item.id,
    });
  }

  const redirectAdmin = () => {
    history.push("/admin/welcome");
  }

  return (
    <div>
      <div style={{ margin: "5px" }}>
        <Logo width={"50px"} height={"50px"} />
      </div>

      <Divider className={classes.styleDivider} />
      <List>
        {Options.map((option, index) => (
          <Fragment>
            {!option.id && (
              <ListItem button key={option.name} onClick={()=>{history.push(option.to)}}>
                <ListItemIcon>{option.icon}</ListItemIcon>
                <ListItemText primary={option.name} />
              </ListItem>
            )}
            {option.id && (
              <ListItem button key={option.name}>
                <ListItemIcon>{option.icon}</ListItemIcon>
                <ListItemText
                  onClick={() => {
                    scrollId(option.to);
                    toggleDrawer(false);
                  }}
                  primary={option.name}
                />
              </ListItem>
            )}
            {option.items && (
              <List component="div" sx={{ pl: 8 }}>
                {option.items.map((item) => {
                  return (
                    <ListItem button onClick={() => { redirect(item) }}>
                      <ListItemText primary={item.name} />
                    </ListItem>
                  );
                })}
              </List>
            )}
          </Fragment>
        ))}
      </List>
      <Divider className={classes.styleDivider} />
      {!online && (
        <ListItem button onClick={toggleDrawerLogin(true)}>
          <ListItemIcon>
            <LoginIcon />
          </ListItemIcon>
          <ListItemText primary="Ingresar" />
        </ListItem >
      )}
      {isAdmin && (
        <ListItem button onClick={redirectAdmin}>
          <ListItemIcon>
          <AdminPanelSettingsIcon sx={{ color: "#616161" }} />
          </ListItemIcon>
          <ListItemText  primary="Panel de administración"/>
        </ListItem>
      )}
      <Divider className={classes.styleDivider} />
    </div>
  );
};

export default DrawerContent;
