import Box from '@mui/material/Box'
import React,{Fragment} from 'react'
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';
import Items from './Items';
import nav from './_navUser';
import Link from '@mui/material/Link';
import {scrollId} from '../../service/utils/funtions.js'

const Options = () => {
    
    return (
        <Box sx={{ flexGrow: 1, display: { xs: 'flex'} }}>
            {nav.map((option) => (
                <Fragment>
                    {!option.id&&<MenuItem key={option.name} >
                    {!option.items&&<Typography 
                            textAlign="center"
                        >
                        <span style={{marginRight:"2px"}}>{option.icon}</span>
                        <Link href={option.to} underline="none" sx={{color:"#fff"}}>{option.name}</Link>
                        </Typography>}
                    { option.items &&
                            <Items option={option}/>
                    }
                    </MenuItem>}
                    {option.id&&<MenuItem key={option.name}  >
                    <Typography 
                        textAlign="center"
                        onClick={()=>scrollId(option.to)}
                    >
                        <span style={{marginRight:"2px"}}>{option.icon}</span>
                        {option.name}
                        </Typography>
                    </MenuItem>}
                </Fragment>
              ))}
        </Box>
    )
}

export default Options
