import * as React from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import {useHistory} from 'react-router-dom';


export default function BasicMenu({option}) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const history = useHistory();
  const [expand,setExpand] = React.useState(false);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setExpand(true);
  };
  const handleClose = () => {
    setAnchorEl(null);
    setExpand(false);
  };

  const redirect = (item) =>{
    history.push({
      pathname: item.to,
      state: item.id,
    });
  }

  return (
    <div>
      <Typography
        id="basic-button"
        aria-controls="basic-menu"
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
        
      >
        <span style={{marginRight:"2px"}}>{option.icon}</span>
        {option.name}
        {!expand&& <ExpandMoreIcon sx={{fontSize:"30px"}}/>}
        {expand&& <ExpandLessIcon sx={{fontSize:"30px"}}/> }
      </Typography>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {
            option.items.map((item)=>(
                <MenuItem key={item.name} onClick={()=>{handleClose();redirect(item)}}>{item.name}</MenuItem>
            ))
        }
      </Menu>
    </div>
  );
}